.app-wrapper {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
    "header header"
    "navbur contant";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 2vw;
}
.app-wrapper-content {
    grid-area: contant;
    background-color: lightskyblue;
}
